import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

import { MyApp } from './app.component';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { HttpModule } from '@angular/http';

import { IonicStorageModule } from '@ionic/storage';
import { OneSignal } from '@ionic-native/onesignal';
import { WooCommerceProvider } from '../providers/woocommerce/woocommerce';

@NgModule({
  declarations: [
    MyApp
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/cart/cart.module.ngfactory#CartPageModuleNgFactory', name: 'CartPage', segment: 'cart', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/checkout/checkout.module.ngfactory#CheckoutPageModuleNgFactory', name: 'CheckoutPage', segment: 'checkout', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module.ngfactory#HomePageModuleNgFactory', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/intro/intro.module.ngfactory#IntroPageModuleNgFactory', name: 'IntroPage', segment: 'intro', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/menu/menu.module.ngfactory#MenuPageModuleNgFactory', name: 'MenuPage', segment: 'menu', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module.ngfactory#LoginPageModuleNgFactory', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/order-details/order-details.module.ngfactory#OrderDetailsPageModuleNgFactory', name: 'OrderDetailsPage', segment: 'order-details', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/orders/orders.module.ngfactory#OrdersPageModuleNgFactory', name: 'OrdersPage', segment: 'orders', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/product-details/product-details.module.ngfactory#ProductDetailsPageModuleNgFactory', name: 'ProductDetailsPage', segment: 'product-details', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/products-by-category/products-by-category.module.ngfactory#ProductsByCategoryPageModuleNgFactory', name: 'ProductsByCategoryPage', segment: 'products-by-category', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/search/search.module.ngfactory#SearchPageModuleNgFactory', name: 'SearchPage', segment: 'search', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/signup/signup.module.ngfactory#SignupPageModuleNgFactory', name: 'SignupPage', segment: 'signup', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/thank/thank.module.ngfactory#SearchPageModuleNgFactory', name: 'ThankPage', segment: 'thank', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tour/tour.module.ngfactory#TourPageModuleNgFactory', name: 'TourPage', segment: 'tour', priority: 'low', defaultHistory: [] }
  ]
}),
    IonicStorageModule.forRoot(),
    HttpModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    WooCommerceProvider,
    OneSignal
  ]
})
export class AppModule {}



